.navbar{
    position: fixed;
    top: 0;
    background-color:transparent;
    width: 100%;
    height:80px;
    padding-left: 50px;
    font-size: 1.5em;
    z-index: 1;
}
.scrolling{
    background-color: #1A1B2F;
    transition: all 0.5s ease;
}

.navbar-logo{
    height: 80px;
    line-height: 80px;
    color: #F0FFF3;
    cursor: pointer;
}