.carousel-container {
    display: flex;
    vertical-align: center;
    align-items: center;
    background-color: #1A1B2F;
    padding: 30px 0 30px 0;
}
.carousel-title{
    color: #F0FFF3;
    width: 40%;
    text-align: center;
    font-size: 20px;
    padding: 0 100px 0 100px;
}
.slide-container{
    height: 80%;
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.slide-container >img{
    object-fit:contain;
    transition: all 0.2s ease-in-out;
}
.slide-container:hover >img{
    transform: scale(1.2);
    opacity: 0.7;
}
.slide-desc{
    background-color: #1A1B2F;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 70%;
    padding-left: 30px;
    border-top-left-radius: 20px;
    opacity: 0.8;
}
.slide-desc > span{
    color: #F0FFF3;
    display: block;
    font-size: 15px;
    padding: 20px 0 20px 10px;
}
.BrainhubCarousel__arrows{
    background-color: #F0FFF3;
    border-radius:50%;
    margin: 20px;
}
.BrainhubCarousel__arrows > span{
    border-color: #1A1B2F;
}
.BrainhubCarousel__arrows:hover{
    background-color: #F0FFF3 !important;
}
.BrainhubCarousel__arrows:hover>span{
    background-color: #1A1B2F !important;
}

@media only screen and (max-width: 960px) {
    .carousel-container{
        flex-direction: column;
    }
    .carousel-title{
        width: 100%;
        text-align: center;
        padding: 50px 0 50px 0;
    }
    .slide-container{
        width: 100%;
    }
}