.footer{
    width: 100%;
    height: 100vh;
    background-color:#1A1B2F;
    color: #F0FFF3;
    padding-top: 180px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footer-info{
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-info > h1{
    padding-bottom: 15px;
}
.footer-contact{
    font-size: 20px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}
.footer-sns{
    width: 70%;
    position: absolute;
    bottom:40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}
.sns-links{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
}
.likedin, .twitter, .facebook{
    color: #F0FFF3;
    font-size:35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}
.likedin:hover, .twitter:hover, .facebook:hover{
    border-color:#F0FFF3;
    transition: all 0.2s ease-in-out;
}
.design-by{
    font-size:18px;
    padding-left: 50px;
    color:#F0FFF3;
}

@media only screen and (max-width:960px){
    .footer-info{
        text-align:center;
        padding: 0;
        font-size:20px;
    }
    .footer-contact{
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align:center;
    }
    .footer-sns{
        grid-template-columns: 1fr;
        text-align:center;
    }
    .design-by{
        padding: 0 0 40px 0;
    }
}