
:root{
    --blue-dark:#10375c;
    --violet-dark:#27045B;
    --white-light:#F0FFF3;
    --black-dark:#1A1B2F;
}
.cover-container {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}

.video{
    object-fit: cover;
    width: 100%;
    height:100vh;
    position: absolute;
    z-index: -1;
}

.cover-container > h1{
    color: var(--white-light);
    letter-spacing: 2px;
    font-size: 5em;
}
.cover-container>p{
    margin-top: 8px;
    color: var(--white-light);
    font-size: 1.5em;
    display: block;
    white-space: nowrap;
    border-right: 4px solid;
    width: 28ch;
    text-align: center;
    animation: typing 2s steps(28),
    blink .5s infinite step-end alternate;
    overflow: hidden;
    margin: 0 auto;
}
@keyframes typing {
    from {
        width:0;
    }
}
@keyframes blink {
    50%{
        border-color: transparent;
    }
}

@media screen and (max-width:960px) {
    .cover-container> h1{
        font-size:70px;
        text-align: center;
    }
    .cover-container>p{
        text-align:center;
        font-size:20px;
        width: 80%;
    }
}